import Swiper from 'swiper';
import {ResponsiveSlider} from "../ResponsiveSlider";

document.addEventListener("DOMContentLoaded", (event) => {

    function handleSlideChange(swiper) {
        if (swiper.isEnd) {
            swiper.el.classList.add('reached-end');
        } else {
            swiper.el.classList.remove('reached-end');
        }
    }

    if (document.querySelector('.product__slider .swiper-container') && document.querySelector('.product__thumbs .swiper-container')) {
        const productSwiper = new Swiper('.product__slider .swiper-container', {
            speed: 400,
            spaceBetween: 10,
            loop: false,
            navigation: {
                prevEl: '.swiper-prev',
                nextEl: '.swiper-next',
            },
        });

        const thumbsSwiper = new Swiper('.product__thumbs .swiper-container', {
            speed: 400,
            loop: false,
            loopedSlides: 4,
            spaceBetween: 24,
            slidesPerView: "auto",
            centeredSlides: false,
            touchRatio: 0.2,
            slideToClickedSlide: true,
            breakpoints: {
                768: {
                    slidesPerView: 3,
                }
            },
            navigation: {
                nextEl: '.product__thumbs .nav-next',
                prevEl: '.product__thumbs .nav-prev',
            },
            on: {
                slideChange: function () {
                    handleSlideChange(this);
                },
                resize: function () {
                    this.update();
                }
            }
        });
        productSwiper.controller.control = thumbsSwiper;
        thumbsSwiper.controller.control = productSwiper;

        thumbsSwiper.on('click', (swiper, event) => {
            const clickedIndex = swiper.clickedIndex;
            if (clickedIndex !== undefined) {
                productSwiper.slideTo(clickedIndex);
            }
        });

        window.addEventListener('resize', function () {
            thumbsSwiper.update();
        });
    }

    if (document.querySelector('.product__variants-list .swiper-container')) {

        const productVariantsSwiper = new Swiper('.product__variants-list .swiper-container', {
            speed: 400,
            spaceBetween: 15,
            slidesPerView: 'auto',
            navigation: {
                nextEl: '.product__variants-list .nav-next',
                prevEl: '.product__variants-list .nav-prev',
            },
            on: {
                slideChange: function () {
                    handleSlideChange(this);
                },
                resize: function () {
                    this.update();
                }
            }
        });


// Обработка изменения размера окна
        window.addEventListener('resize', function () {
            productVariantsSwiper.update();
        });
    }


    const productVariantsSwiper = new Swiper('.product__variants-list .swiper-container', {
        speed: 400,
        spaceBetween: 15,
        slidesPerView: 'auto',
        navigation: {
            nextEl: '.product__variants-list .nav-next',
            prevEl: '.product__variants-list .nav-prev',
        },
        on: {
            slideChange: function () {
                handleSlideChange(this);
            },
            resize: function () {
                this.update();
            }
        }
    });


    new ResponsiveSlider('.description-block').initSwipers({
        spaceBetween: 10,
        slidesPerView: "auto",
        slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
        slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
        breakpoints: {
            769: {
                spaceBetween: 20,
                slidesPerView: 3,
                slidesOffsetBefore: 0,
                slidesOffsetAfter: 0,
            },
            993: {
                spaceBetween: 40,
                slidesPerView: 3,
                slidesOffsetBefore: 0,
                slidesOffsetAfter: 0,
            }
        },
        pagination: {
            el: '.description-block__slider .swiper-pagination-circles',
            type: 'bullets',
        },
    });

});