import {slideDown, slideUp} from "../utils";
import debounce from "lodash/debounce";

document.addEventListener("DOMContentLoaded", (event) => {


    const filterAccordions = document.querySelectorAll('.filter-accordion');
    if (filterAccordions.length) {
        filterAccordions.forEach(filterAccordion => {
            const btn = filterAccordion.querySelector('.filter-accordion__current');
            const body = filterAccordion.querySelector('.filter-accordion__body');
            const items = filterAccordion.querySelectorAll('.catalog-sections__item');
            if (body && btn) {
                btn.addEventListener('click', () => {
                    if (filterAccordion.hasAttribute('data-open')) {
                        filterAccordion.removeAttribute('data-open');
                    } else {
                        filterAccordion.setAttribute('data-open', true);
                    }
                });
                items.forEach(item => {
                    item.addEventListener('change', (evt) => {
                        setTimeout(() => {
                            let labelText = item.querySelector('label').textContent;
                            if(labelText){
                                btn.textContent = labelText;
                                filterAccordion.removeAttribute('data-open');
                            }
                        }, 50);
                    });
                })
            }
        });
    }

    const filterBox = document.querySelector('.catalog-sort');
    if (filterBox) {
        const filterBoxTop = filterBox.querySelector('.catalog-sort__current');
        const filterBoxOpenAdditional = filterBox.querySelector('.catalog-sort__label');
        const filterBoxList = filterBox.querySelector('.catalog-sort__body');
        const filterBoxItems = filterBox.querySelectorAll('.catalog-sort__item-to-choose');
        const clickOutBoxHandler = ({target}) => {
            if (!target.classList.contains('catalog-sort__current') && !target.closest('.catalog-sort__current') &&
                !target.classList.contains('catalog-sort__label') && !target.closest('.catalog-sort__label')) {
                filterBoxList.removeAttribute('data-active');
            }
        };

        filterBoxItems.forEach(filterBoxItem => {
            filterBoxItem.addEventListener('click', (evt) => {
                if (!filterBoxItem.hasAttribute('data-active')) {
                    const activeCur = filterBoxList.querySelector('[data-active]');
                    activeCur?.removeAttribute('data-active');
                    filterBoxItem.setAttribute('data-active', true);
                    filterBoxTop.textContent = filterBoxItem.textContent;
                }
            });
        })

        if (filterBoxOpenAdditional) {
            filterBoxOpenAdditional.addEventListener('click', (evt) => {
                if (filterBoxList.getAttribute('data-active')) {
                    filterBoxList.removeAttribute('data-active');
                } else {
                    filterBoxList.setAttribute('data-active', true);
                    document.addEventListener('click', clickOutBoxHandler);
                }
            });
        }

        filterBoxTop.addEventListener('click', (evt) => {
            if (filterBoxList.getAttribute('data-active')) {
                filterBoxList.removeAttribute('data-active');
            } else {
                filterBoxList.setAttribute('data-active', true);
                document.addEventListener('click', clickOutBoxHandler);
            }
        });
    }

    const filterBlocks = document.querySelectorAll('.filter-group__block');
    if (filterBlocks.length) {
        filterBlocks.forEach(filterBlock => {
            const tumblr = filterBlock.querySelector('.filter-group__block__header');
            if (tumblr) {
                tumblr.addEventListener('click', () => {
                    if (filterBlock.classList.contains('filter-group__block--active')) {
                        filterBlock.classList.remove('filter-group__block--active')
                    } else {
                        filterBlock.classList.add('filter-group__block--active')
                    }
                });
            }
        });
    }

    const additionalBurgers = document.querySelectorAll('[data-burger-wrapper]');
    if (additionalBurgers.length) {
        additionalBurgers.forEach(additionalBurger => {
            const btns = additionalBurger.querySelectorAll('[data-burger-btn]');
            const burger = additionalBurger.querySelector('[data-burger]');
            const close = additionalBurger.querySelector('[data-burger-close]');
            const body = document.querySelector('body');
            if (btns.length && burger) {
                btns.forEach(btn => {
                    btn.addEventListener('click', () => {
                        burger.setAttribute('data-opened', true);
                        body.classList.add('lock');
                    });
                })
            }
            if (close && burger) {
                close.addEventListener('click', () => {
                    burger.removeAttribute('data-opened');
                    body.classList.remove('lock');
                });
            }
        });
    }

    const alignSubPhotos = (packageCard) => {
        const subphotosBox = packageCard.querySelector('.product-card__img');
        if (subphotosBox) {
            const subphotos = packageCard.querySelectorAll('.product-card__sub-photo');
            if (subphotos.length > 3) {
                const style = window.getComputedStyle(subphotosBox);
                const boxHeight = parseFloat(style.height);
                const rows = Math.ceil(subphotos.length / 3);
                const subHeight = boxHeight / rows;
                subphotos.forEach(subphoto => {
                    subphoto.style.height = `${subHeight}px`;
                });
            }
        }
    }

    const packages = document.querySelectorAll('.product-card--package');
    if (packages.length) {
        packages.forEach(packageCard => {
            alignSubPhotos(packageCard);
        });
        window.addEventListener('resize' , debounce(() => {
            packages.forEach(packageCard => {
                alignSubPhotos(packageCard);
            });
        }), 400);
    }


});