import Swiper from 'swiper';
import {ResponsiveSlider} from "../ResponsiveSlider";

document.addEventListener("DOMContentLoaded", (event) => {
    function handleSlideChange(swiper) {
        if (swiper.isEnd) {
            swiper.el.closest('.card-welcome').classList.add('reached-end');
        } else {
            swiper.el.closest('.card-welcome').classList.remove('reached-end');
        }
    }

    if (document.querySelector('.card-welcome__slider.swiper') && document.querySelector('.card-welcome__mini-gallery .swiper')) {
        const productSwiper = new Swiper('.card-welcome__slider.swiper', {
            speed: 400,
            spaceBetween: 10,
            loop: false,
            navigation: {
                prevEl: '.card-welcome__left .card-welcome__prev',
                nextEl: '.card-welcome__left .card-welcome__next',
            },
            on: {
                slideChange: function () {
                    handleSlideChange(this);
                },
                resize: function () {
                    this.update();
                }
            }
        });

        const thumbsSwiper = new Swiper('.card-welcome__mini-gallery .swiper', {
            speed: 400,
            loop: false,
            spaceBetween: 10,
            slidesPerView: 3,
            centeredSlides: false,
            touchRatio: 0.2,
            slideToClickedSlide: true,
            breakpoints: {
                600: {
                    spaceBetween: 20,
                    slidesPerView: 4,
                },
                768: {
                    spaceBetween: 24,
                    slidesPerView: 4,
                }
            },
            pagination: {
                el: '.card-welcome__mini-gallery .swiper-pagination-circles',
                type: 'bullets',
            },
            on: {
                slideChange: function () {
                    handleSlideChange(this);
                },
                resize: function () {
                    this.update();
                }
            }
        });
        productSwiper.controller.control = thumbsSwiper;
        thumbsSwiper.controller.control = productSwiper;

        thumbsSwiper.on('click', (swiper, event) => {
            const clickedIndex = swiper.clickedIndex;
            if (clickedIndex !== undefined) {
                productSwiper.slideTo(clickedIndex);
            }
        });

        window.addEventListener('resize', function () {
            thumbsSwiper.update();
        });
    }

    new Swiper('.capabilities-slider', {
        speed: 400,
        loop: true,
        navigation: {
            prevEl: '.capabilities-slider__prev',
            nextEl: '.capabilities-slider__next',
        },
    });


    new ResponsiveSlider('.goods-slider', '.goods .container').initSwipers({
        spaceBetween: 10,
        slidesPerView: 1,
        slidesOffsetBefore: 0,
        slidesOffsetAfter: 0,
        breakpoints: {
            600: {
                spaceBetween: 10,
                slidesPerView: 2,
                slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
                slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
            },
            769: {
                spaceBetween: 20,
                slidesPerView: "auto",
                slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
                slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
            },
            993: {
                spaceBetween: 40,
                slidesPerView: "auto",
                slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
                slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
            }
        },
        navigation: {
            prevEl: '.goods-slider .goods__prev',
            nextEl: '.goods-slider .goods__next',
        },
        pagination: {
            el: '.goods-slider .swiper-pagination-circles',
            type: 'bullets',
        },
    });

    function handleSlideChangeProjects(swiper) {
        if (swiper.isEnd) {
            swiper.el.classList.add('reached-end');
        } else {
            swiper.el.classList.remove('reached-end');
        }
    }

    new ResponsiveSlider('.projects', '.projects .container').initSwipers({
        slidesOffsetBefore: 0,
        slidesOffsetAfter: 0,
        watchOverflow: true,
        speed: 400,
        slidesPerView: 1,
        spaceBetween: 10,
        breakpoints: {
            600: {
                spaceBetween: 20,
                slidesPerView: "auto",
                slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
                slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
            },
            993: {
                spaceBetween: 40,
                slidesPerView: 4,
                slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
                slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
            }
        },
        navigation: {
            prevEl: '.projects .projects__prev',
            nextEl: '.projects .projects__next',
        },
        pagination: {
            el: '.projects .swiper-pagination-circles',
            type: 'bullets',
        },
        on: {
            slideChange: function () {
                handleSlideChangeProjects(this);
            },
            resize: function () {
                this.update();
            }
        }
    });


});