require('./scss/styles.scss');
import Swiper from 'swiper';
import {
    Thumbs,
    Grid,
    Navigation,
    Pagination,
    Autoplay,
    FreeMode,
    EffectCards,
    EffectFade,
    Controller
} from 'swiper/modules';
// import Choices from "choices.js";
// import 'choices.js/public/assets/styles/choices.min.css'
import {SeasonsHeader} from "./js/header";
import debounce from "lodash/debounce";
import Inputmask from "inputmask/lib/inputmask.js";
import {ResponsiveSlider} from "./js/ResponsiveSlider";
import * as profile from "./js/components/product-card";
import * as project from "./js/components/project-card";
import * as catalog from "./js/components/catalog";

Swiper.use([Thumbs, Navigation, Pagination, Autoplay, FreeMode, EffectCards, EffectFade, Controller]);


document.addEventListener("DOMContentLoaded", (event) => {

        const header = document.querySelector('.header');
        new SeasonsHeader(header);

        new Swiper('.hero__slider .swiper', {
            speed: 400,
            spaceBetween: 0,
            preloadImages: false,
            lazy: true,
            autoplay: {
                delay: 3000,
            },
            pagination: {
                el: '.hero__slider .swiper-pagination',
                type: 'bullets',
            },
        });

        new ResponsiveSlider('.promo:not(.promo--type-2)').initSwipers({
            spaceBetween: 30,
            slidesPerView: "auto",
            slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
            slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
            breakpoints: {
                992: {
                    spaceBetween: 40,
                    slidesPerView: 3,
                    slidesOffsetBefore: 0,
                    slidesOffsetAfter: 0,
                },
                1241: {
                    spaceBetween: 120,
                    slidesPerView: 3,
                    slidesOffsetBefore: 0,
                    slidesOffsetAfter: 0,
                },
            }
        });

        new ResponsiveSlider('.promo--type-2').initSwipers({
            spaceBetween: 30,
            slidesPerView: "auto",
            slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
            slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
            breakpoints: {
                992: {
                    spaceBetween: 40,
                    slidesPerView: 3,
                    slidesOffsetBefore: 0,
                    slidesOffsetAfter: 0,
                },
            }
        });


        new ResponsiveSlider('.our-services__content').initSwipers({
            spaceBetween: 10,
            slidesPerView: "auto",
            slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
            slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
            breakpoints: {
                768: {
                    spaceBetween: 30,
                    slidesPerView: "auto",
                    slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
                    slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
                },
                1240: {
                    spaceBetween: 40,
                    slidesPerView: 4,
                    slidesOffsetBefore: 0,
                    slidesOffsetAfter: 0,
                },
            },
            pagination: {
                el: '.our-services__items .swiper-pagination-circles',
                type: 'bullets',
            },
        });

        new ResponsiveSlider('.slider-section', '.slider-section .container').initSwipers({
            spaceBetween: 10,
            slidesPerView: "auto",
            slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
            slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
            breakpoints: {
                768: {
                    spaceBetween: 30,
                    slidesPerView: "auto",
                    slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
                    slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
                },
                992: {
                    spaceBetween: 40,
                    slidesPerView: "auto",
                    slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
                    slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
                },
                1240: {
                    spaceBetween: 60,
                    slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
                    slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
                },
            },
            pagination: {
                el: '.slider-section__content .swiper-pagination-circles',
                type: 'bullets',
            },
        }, true);


        new Swiper(".reviews__swiper.swiper", {
            slidesPerView: 1,
            navigation: {
                nextEl: '.reviews__navigation .nav-next',
                prevEl: '.reviews__navigation .nav-prev',
            },
        });

        new ResponsiveSlider('.experts-slider').initSwipers({
            slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
            slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
            spaceBetween: 30,
            slidesPerView: "auto",
            breakpoints: {
                // when window width is >= 320px
                601: {
                    spaceBetween: 30,
                    slidesPerView: 2,
                    slidesOffsetBefore: 0,
                    slidesOffsetAfter: 0,
                },
                801: {
                    spaceBetween: 36,
                    slidesPerView: 3,
                    slidesOffsetBefore: 0,
                    slidesOffsetAfter: 0,
                },
                1241: {
                    spaceBetween: 48,
                    slidesPerView: 4,
                    slidesOffsetBefore: 0,
                    slidesOffsetAfter: 0,
                },
            }
        }, true);


        function scrollToElementWithHeaderOffset(element, header) {
            const headerHeight = document.querySelector('header').offsetHeight; // Высота хедера
            const elementPosition = element.getBoundingClientRect().top; // Позиция элемента относительно верхней границы окна
            const offsetPosition = elementPosition - headerHeight; // Позиция с учетом высоты хедера

            setTimeout(() => {
                window.scrollBy({
                    top: offsetPosition,
                    behavior: 'smooth'
                });
            }, 0);
        }

        const tabsBlock = document.querySelectorAll('[data-tabs]');
        if (tabsBlock.length) {
            tabsBlock.forEach(tabsBlock => {
                const btns = tabsBlock.querySelectorAll('[data-tab]');
                const blocks = tabsBlock.querySelectorAll('[data-tab-block]');
                for (let btn of btns) {
                    btn.addEventListener('click', () => {
                        const id = parseInt(btn.getAttribute('data-tab'));
                        for (let block of blocks) {
                            if (parseInt(block.getAttribute('data-tab-block')) === id) {
                                block.setAttribute('data-active', '');
                                scrollToElementWithHeaderOffset(tabsBlock);
                            } else {
                                block.removeAttribute('data-active');
                            }
                        }
                        btn.setAttribute('data-active', '');
                        for (let otherBtn of btns) {
                            if (parseInt(otherBtn.getAttribute('data-tab')) !== id) {
                                otherBtn.removeAttribute('data-active');
                            }
                        }
                    });
                }
            });
        }

        const doProportionIcon = (icons) => {
            for (const icon of icons) {
                const widthAttr = icon.getAttribute('width');
                const widthReal = icon.clientWidth;
                const heightAttr = icon.getAttribute('height');
                const heightReal = (widthReal / widthAttr) * heightAttr;
                icon.style.height = `${heightReal}px`;
            }
        }


        const icons = document.querySelectorAll('.icon-pr');
        if (icons.length) {
            doProportionIcon(icons);
            window.addEventListener('resize', debounce(() => {
                doProportionIcon(icons);
            }, 300));
        }

        const showRoomImg = document.querySelectorAll('.showroom-pictures__small img');
        if (showRoomImg.length) {
            showRoomImg.forEach((showRoomImage) => {
                const showRoomImgContainer = document.querySelector('.showroom-pictures__big img');
                showRoomImage.addEventListener('click', (evt) => {
                    const showRoomThumbsContainer = showRoomImage.closest('.showroom-pictures__list');
                    showRoomThumbsContainer.querySelector('.showroom-pictures__small.active').classList.remove('active');
                    showRoomImage.closest('.showroom-pictures__small').classList.add('active');
                    const newSrc = showRoomImage.src;
                    showRoomImgContainer.src = newSrc;
                    const newSrcSet = showRoomImage.srcset;
                    showRoomImgContainer.srcset = newSrcSet;
                });
                showRoomImage.addEventListener('mouseover', (evt) => {
                    const showRoomThumbsContainer = showRoomImage.closest('.showroom-pictures__list');
                    showRoomThumbsContainer.querySelector('.showroom-pictures__small.active').classList.remove('active');
                    showRoomImage.closest('.showroom-pictures__small').classList.add('active');
                    const newSrc = showRoomImage.src;
                    showRoomImgContainer.src = newSrc;
                    const newSrcSet = showRoomImage.srcset;
                    showRoomImgContainer.srcset = newSrcSet;
                });
            });
        }

        new ResponsiveSlider('.brand-page__photos-slider').initSwipers({
            spaceBetween: 10,
            slidesPerView: "auto",
            slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
            slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
            breakpoints: {
                769: {
                    spaceBetween: 20,
                    slidesPerView: 3,
                    slidesOffsetBefore: 0,
                    slidesOffsetAfter: 0,
                },
                993: {
                    spaceBetween: 40,
                    slidesPerView: 3,
                    slidesOffsetBefore: 0,
                    slidesOffsetAfter: 0,
                }
            },
            pagination: {
                el: '.brand-page__photos-slider .swiper-pagination-circles',
                type: 'bullets',
            },
        });

        new ResponsiveSlider('.brands-card').initSwipers({
            spaceBetween: 10,
            slidesPerView: "auto",
            slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
            slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
            breakpoints: {
                769: {
                    spaceBetween: 20,
                    slidesPerView: 4,
                    slidesOffsetBefore: ResponsiveSlider.getOffsetKey(),
                    slidesOffsetAfter: ResponsiveSlider.getOffsetKey(),
                },
                993: {
                    spaceBetween: 40,
                    slidesPerView: 4,
                    slidesOffsetBefore: 0,
                    slidesOffsetAfter: 0,
                }
            },
        }, false, '.swiper-pagination-circles');

        const moreInFilters = document.querySelectorAll('[data-more-btn]');
        if (moreInFilters.length) {
            moreInFilters.forEach(moreInFilter => {
                    const accord = moreInFilter.closest('[data-patchy-wrapper]');
                    if (accord) {
                        moreInFilter.addEventListener('click', (evt) => {
                            if (accord.hasAttribute('data-full')) {
                                accord.removeAttribute('data-full');
                                moreInFilter.textContent = moreInFilter.getAttribute('data-more-text');
                            } else {
                                accord.setAttribute('data-full', true);
                                moreInFilter.textContent = moreInFilter.getAttribute('data-less-text');
                            }
                        })
                    }
                }
            )
        }

        const revTextMoreBtns = document.querySelectorAll('.partner-review__more-btn');
        if(revTextMoreBtns.length){
            revTextMoreBtns.forEach(revTextMoreBtn => {
                const textBox = revTextMoreBtn.closest('.partner-review__text');
                revTextMoreBtn.addEventListener('click', () => {
                    textBox.setAttribute('data-full-text', true);
                });
            })
        }

    }
);