import debounce from "lodash/debounce";
import {slideUp, slideDown} from "./utils";

export class SeasonsHeader {
    constructor(header) {
        this._header = header;
        this._scrolledLogic = this._scrolledLogic.bind(this);
        this._clickOutBasket = this._clickOutBasket.bind(this);
        this._body = document.querySelector('body');
        this._burgerIcon = this._header.querySelector('.header-burger-icon');
        this._burger = this._header.querySelector('.header__nav');
        this._addHandlers();
        this._isDesktop = window.innerWidth >= 992;
        this._isMobile = window.innerWidth < 768;
        this._scrolledLogic();
    }

    _clickOutBurger = ({target}) => {
        if (!this._header.hasAttribute('data-animating')) {
            if (!target.classList.contains('header') && !target.closest('header')) {
                this._closeBurger();
            }
        }
    }

    _openBurger() {
        this._header.setAttribute('data-open', true);
        this._burgerIcon.setAttribute('data-open', true);
        this._body.classList.add('blocked');
        slideDown(this._burger, 300, () => {
            document.addEventListener('click', this._clickOutBurger);
            this._header.removeAttribute('data-animating');
        })

    }

    _resetStylesForDesktop() {
        this._burger.style = '';
        this._burgerIcon.removeAttribute('data-open');
        this._header.removeAttribute('data-open');
        this._body.classList.remove('blocked');
        this._isDesktop = true;
    }

    _closeBurger() {
        this._burgerIcon.removeAttribute('data-open');
        document.removeEventListener('click', this._clickOutBurger);
        slideUp(this._burger, 300, () =>{
            this._header.removeAttribute('data-animating');
            this._header.removeAttribute('data-open');
            this._body.classList.remove('blocked');
        })
    }


    _addHandlers() {
        this._burgerIcon.addEventListener('click', () => {
            if (!this._header.hasAttribute('data-animating')) {
                this._header.setAttribute('data-animating', true);
                if (this._header.hasAttribute('data-open')) {
                    this._closeBurger();
                } else {
                    this._openBurger();
                }
            }
        })

        window.addEventListener('resize', debounce(() => {
            this._isDesktop = window.innerWidth >= 992;

            if (window.innerWidth < 992) {
            }

            this._isMobile = window.innerWidth < 768;
            if (this._isDesktop) {
                this._resetStylesForDesktop();
            }

        }, 100));


        window.addEventListener('scroll', this._scrolledLogic);
        this._header.addEventListener('animationstart', () => {
            this._header.classList.add('animation-collapsing');
        });
        this._header.addEventListener('animationend', () => {
            this._header.classList.remove('animation-collapsing');
            this._header.style.top = 0;
        });
    }

    _clickOutBasket({target}) {
        // if (!this._catalog.hasAttribute('data-animating')) {
        //     if (!target.classList.contains('header') && !target.closest('header')) {
        //     }
        // }
    }

    _scrolledLogic() {
        const headerHeight = this._header.getBoundingClientRect().height;
        window.addEventListener('scroll', () => {
            const topScrolled = window.pageYOffset;
            if (!this._header.classList.contains('header--scrolled')) {
                if (topScrolled < headerHeight) {
                    this._header.style.top = `-${topScrolled}px`;
                } else {
                    this._header.classList.add('header--scrolled');
                    document.documentElement.style.setProperty(
                        '--header-height',
                        `${this._header.offsetHeight}px`
                    );
                }
            } else if (
                topScrolled === 0 &&
                !this._header.classList.contains('animation-collapsing')
            ) {
                this._header.classList.remove('header--scrolled');
            }
        });
    }
}